var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"info base_width"},[_c('h3',{staticClass:"header font-weight-normal"},[_vm._v("哈佛大学发公告感谢恒大 正携手钟南山共同战“疫”")]),_c('h5',{staticClass:"tips font-weight-normal"},[_vm._v(" 发布时间：2020-03-09 内容来源：央广网")]),_c('div',{staticClass:"content"},[_c('div',{staticClass:"box-header"},[_c('p',{staticClass:"indent"},[_vm._v("美国东部时间2月24日，哈佛大学发布官方公告，正式确认正与钟南山院士牵头的中国科研工作者展开合作，共同致力于寻求对抗新冠肺炎疫情的更好诊断和治疗方案。整个科研攻坚的1.15亿美元（约8亿元人民币）科研经费由恒大集团提供支持。")]),_c('div',{staticClass:"hd_news_company_info"},[_c('div',{staticClass:"hd_new_company_info_img bg_cover",style:({width: '514px',height: '947px',backgroundImage: ("url(" + (require('../../assets/images/news/news15.jpg')) + ")")})})]),_vm._m(0),_vm._m(1),_c('p',{staticClass:"indent"},[_vm._v("自2019年12月被发现以来，新型冠状病毒肺炎迅速发展成为一场国际关注的公共卫生危机，造成78000人感染，超过2300人死亡，并可能对医疗系统和经济发展造成更大影响。为了应对这些挑战，哈佛的科学家们将和中国的同行们联手，一道探索开发新的诊疗方案，以遏制病毒扩散传播并治疗已感染病患。")]),_c('p',{staticClass:"indent"},[_vm._v("美国方面的工作主要由George Daley 院长带领下的哈佛医学院的科学家们带头开展，由哈佛TH Chan公共卫生学院配合。哈佛医学院将作为美国团队的中心枢纽，召集医学院、哈佛附属医院和众多本地区的医疗机构和生物科技公司的基础科研工作者，转换医学和临床研究学者等专家共同攻关。中国方面的工作将由广州呼吸健康研究院和著名肺病专家和流行病专家钟南山院士牵头。钟院士是中国本次新型冠状病毒高级专家组组长和呼吸疾病国家重点实验室主任。")]),_c('p',{staticClass:"indent"},[_vm._v("此次哈佛和广州呼研院的合作由世界500强企业中国恒大集团提供为期五年共 1.15亿美元资金支持。")]),_c('p',{staticClass:"indent"},[_vm._v("“我们相信哈佛和广州呼吸研究院的合作,一定会为此次抗击疫情贡献重要的发现。”哈佛大学校长Lawrence Bacow表示。“我们对恒大在协调和推动这次合作中表现出的领导力和慷慨贡献表示由衷的感谢；对所有此次响应号召的科学家和医疗人员, 为他们迅速行动共同对抗这次对全人类形成威胁的疫情表示感谢。”")]),_c('p',{staticClass:"indent"},[_vm._v("“恒大非常荣幸有机会在对抗全球性的健康危机中做出贡献，”恒大集团董事局主席许家印说到，“我们对所有迅速和积极响应合作的哈佛科学家们表示感谢，为哈佛和钟院士团队为了全人类的健康事业做出的奉献深受感动。我们坚信这个国际化的合作一定会尽快收获对抗疫情的突破性发现。”")]),_c('p',{staticClass:"indent"},[_vm._v("正式合作细节将陆续确定，但整个合作的指引目标为解释新型冠状病毒的基础结构及其表现，并在以下五个方面研究疾病检测和治疗：")]),_c('p',{staticClass:"indent"},[_vm._v("a.快速且更为精确的诊断试剂的研发，包括现场测试")]),_c('p',{staticClass:"indent"},[_vm._v("b.了解机体的免疫反应和宿主-病原体交流，探索监测传染路径和疾病发展并预测危重症并发症的生物标签")]),_c('p',{staticClass:"indent"},[_vm._v("c.可预防感染的疫苗")]),_c('p',{staticClass:"indent"},[_vm._v("d.可以帮助缩短发病期和缓解症状的抗病毒疗法研究")]),_c('p',{staticClass:"indent"},[_vm._v("e.重症病人的针对性疗法")]),_c('p',{staticClass:"indent"},[_vm._v("“哈佛医学院在相关领域拥有大量的顶尖临床和科研学者，使本院有独特的号召力，召集病毒学、传染病学、 结构生物学、病理学、疫苗开发、 流行病学和公共卫生等领域的专家携手面对这个快速传播的疫情危机”，George Daley 表示，“我们医学院的宗旨就是专注于提升全人类的健康和福祉，而利用科研方式来应对全球健康挑战是这个宗旨的核心所在”。")]),_c('p',{staticClass:"indent"},[_vm._v("“恒大积极协调和大力支持中美的合作以及哈佛的同仁们积极响应都让我们深受鼓舞。”钟南山院士表示。钟院士在2003年确认了SARS冠状病毒的病原体并确定了病毒的临床病程。“我们期待各方各展所长，应对近期和长期的病毒研究挑战，也期待合作能尽早收效，进一步改善全世界人民的健康和生活。”")]),_c('p',{staticClass:"indent"},[_vm._v("哈佛大学常务副校长Alan Garber 表示新型传染病的爆发有可能快速传播，带来致命的后果。")]),_c('p',{staticClass:"indent"},[_vm._v("“这意味着应对反应必须是全球性的，迅速的，而且有一流科研技术支持的。 我们相信此次合作，涵盖了哈佛和其各附属机构，本地区和美国的组织，以及战斗在一线的中国科学家和临床医生，为我们能尽快控制病毒疫情带来希望,” Garber 表示。“我们在这次疫情中获得的知识将使我们在未来面对传染病紧急情况时有更好更及时的应对”。")]),_c('div',{staticClass:"hd_news_company_info"},[_c('div',{staticClass:"hd_new_company_info_img bg_cover",style:({width: '700px',height: '2225px',backgroundImage: ("url(" + (require('../../assets/images/news/news14.jpg')) + ")")})})])])]),_c('infoPage',{attrs:{"first":_vm.first,"last":_vm.last,"prevUrl":_vm.prevUrl,"nextUrl":_vm.nextUrl}})],1)}
